











































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CString from '@/components/links/CString.vue';
import CButton from '@/components/CButton.vue';
import PrefectureData from '@/typescript/PrefectureData.ts'

@Component({
  components: {
    CString,
    CButton,
  },
  head: {
    title: {
      inner: '地方別都道府県一覧整形',
    },
  },
})
export default class PrefectureGroup extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '出力';
  private dataFormat: {
    before: string,
    main: string,
    after: string,
  } = {
    before: '',
    main: '',
    after: '',
  }
  private dataText: string = '';
  private dataSelectedSampleIndex: number = 0;
  public dataKeyRegExps: {
    iso: RegExp,
    name: RegExp,
    kana: RegExp,
    roman: RegExp,
    capital: RegExp,
    capital_kana: RegExp,
    capital_roman: RegExp,
    region: RegExp,
    region_kana: RegExp,
    region_roman: RegExp,
  } = PrefectureData.keyRegExps;
  private dataPrefectures: {
      iso: string,
      name: string,
      kana: string,
      roman: string,
      capital: string,
      capital_kana: string,
      capital_roman: string,
      region_index: number,
  }[] = PrefectureData.prefectures;
  private dataRegions: {
    name: string,
    kana: string,
    roman: string,
  }[] = PrefectureData.regions;
  private dataSamples: {
    name: string,
    description: string,
    format: {
      before: string,
      main: string,
      after: string,
    },
  }[] = [
    {
      name: 'none',
      description: '',
      format: {
        before: '',
        main: '',
        after: '',
      }
    },
    {
      name: 'select',
      description: 'HTML文書のselectタグ用',
      format: {
        before: '<optgroup label="{ region }">\n',
        main: '<option value="{ roman }">{ name }</option>\n',
        after: '</optgroup>\n',
      }
    },
    {
      name: 'table',
      description: 'HTML文書のtableタグ用(1)',
      format: {
        before: '<table class="prefectures">\n<caption>{ region }（{ region_kana }）</caption>\n',
        main: '<tr>\n    <td class="prefecture-iso">{ iso }</td>\n    <td class="prefecture-name">{ name }</td>\n    <td class="prefecture-kana">{ kana }</td>\n    <td class="prefecture-roman">{ roman }</td>\n    <td class="prefecture-capital">{ capital }</td>\n    <td class="prefecture-capital-kana">{ capital_kana }</td>\n    <td class="prefecture-capital-roman">{ capital_roman }</td>\n</tr>\n',
        after: '</table>\n',
      }
    },
    {
      name: 'table2',
      description: 'HTML文書のtableタグ用(2)',
      format: {
        before: '<tr>\n    <th colspan="7">{ region }</th>\n</tr>\n',
        main: '<tr>\n    <td class="prefecture-iso">{ iso }</td>\n    <td class="prefecture-name">{ name }</td>\n    <td class="prefecture-kana">{ kana }</td>\n    <td class="prefecture-roman">{ roman }</td>\n    <td class="prefecture-capital">{ capital }</td>\n    <td class="prefecture-capital-kana">{ capital_kana }</td>\n    <td class="prefecture-capital-roman">{ capital_roman }</td>\n</tr>\n',
        after: '',
      }
    },
    {
      name: 'markdown',
      description: 'マークダウン記法',
      format: {
        before: '### {region}\n\n',
        main: '- { name }({ kana })\n',
        after: '\n',
      }
    },
  ];

  // Methods
  private listenOnInputFormatBefore($event: Event): void {
    Vue.set(this.dataFormat, 'before', (<HTMLInputElement>(<any>$event).target).value);
  }
  private listenOnInputFormatMain($event: Event): void {
    Vue.set(this.dataFormat, 'main', (<HTMLInputElement>(<any>$event).target).value);
  }
  private listenOnInputFormatAfter($event: Event): void {
    Vue.set(this.dataFormat, 'after', (<HTMLInputElement>(<any>$event).target).value);
  }
  private listenSampleSelect($event: Event): void {
    let index: number = Number((<HTMLInputElement>(<any>$event).target).value);
    this.dataFormat = this.dataSamples[index].format;
    this.dataSelectedSampleIndex = index;
  }
  private outputText(): void {
    this.dataText = '';
    if (this.dataFormat.before !== '' || this.dataFormat.main !== '' || this.dataFormat.after !== '') {
      let regExps: RegExp[] = Object.values(this.dataKeyRegExps);
      for (let i: number = 0; i < regExps.length; i++) {
        if (this.dataFormat.before.match(regExps[i]) || this.dataFormat.main.match(regExps[i]) || this.dataFormat.after.match(regExps[i])) {
          this.formatPrefectures();
          break;
        }
      }
    }
  }
  private formatPrefectures(): void {
    let text: string = '';
    let regionBeforeText: string = '';
    let regionAfterText: string = '';
    let regionIndex: number = -1;
    for (let i: number = 0; i < this.dataPrefectures.length; i++) {
      let prefectureTextMain: string = this.dataFormat.main;
      if (regionIndex < this.dataPrefectures[i].region_index) {
        text += regionAfterText;
        regionBeforeText = this.dataFormat.before;
        regionBeforeText = regionBeforeText.replace(this.dataKeyRegExps.region, this.dataRegions[this.dataPrefectures[i].region_index].name);
        regionBeforeText = regionBeforeText.replace(this.dataKeyRegExps.region_kana, this.dataRegions[this.dataPrefectures[i].region_index].kana);
        regionBeforeText = regionBeforeText.replace(this.dataKeyRegExps.region_roman, this.dataRegions[this.dataPrefectures[i].region_index].roman);
        text += regionBeforeText;
        regionAfterText = this.dataFormat.after;
        regionAfterText = regionAfterText.replace(this.dataKeyRegExps.region, this.dataRegions[this.dataPrefectures[i].region_index].name);
        regionAfterText = regionAfterText.replace(this.dataKeyRegExps.region_kana, this.dataRegions[this.dataPrefectures[i].region_index].kana);
        regionAfterText = regionAfterText.replace(this.dataKeyRegExps.region_roman, this.dataRegions[this.dataPrefectures[i].region_index].roman);
        regionIndex++;
      }
      prefectureTextMain = prefectureTextMain.replace(this.dataKeyRegExps.iso, this.dataPrefectures[i].iso);
      prefectureTextMain = prefectureTextMain.replace(this.dataKeyRegExps.name, this.dataPrefectures[i].name);
      prefectureTextMain = prefectureTextMain.replace(this.dataKeyRegExps.kana, this.dataPrefectures[i].kana);
      prefectureTextMain = prefectureTextMain.replace(this.dataKeyRegExps.roman, this.dataPrefectures[i].roman);
      prefectureTextMain = prefectureTextMain.replace(this.dataKeyRegExps.capital, this.dataPrefectures[i].capital);
      prefectureTextMain = prefectureTextMain.replace(this.dataKeyRegExps.capital_kana, this.dataPrefectures[i].capital_kana);
      prefectureTextMain = prefectureTextMain.replace(this.dataKeyRegExps.capital_roman, this.dataPrefectures[i].capital_roman);
      text += prefectureTextMain;
    }
    text += regionAfterText;
    this.dataText = text;
  }
}
